/******************
    Variable css
********************/
:root {
  --theme-color: 255, 141, 47;
  --secondary-color: 65, 68, 73;
  --content-color: 141, 143, 145;
  --white: 255, 255, 255;
  --black: 0, 0, 0;
}

/************************ 
    Reset css 
***********************/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section,
.section-t-space {
  padding-top: calc(20px + 60 * (100vw - 320px) / 1600);
}

.section-b-space {
  padding-bottom: calc(20px + 60 * (100vw - 320px) / 1600);
}

.custom-container {
  padding: 0 calc(20px + 60 * (100vw - 320px) / 1600);
}

@media (min-width: 1560px) {
  .container {
    max-width: 1420px;
  }
}
.qr-btn {
  position: relative;
}
.qr-btn .qr-code {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 100px;
  border: 1px solid black;
  border-radius: 10px;
  z-index: 3;
}
@media (max-width: 1100px) {
  .qr-btn .qr-code {
    display: none;
  }
}

/************************ 
    Typography css 
***********************/
body {
  position: relative;
  font-family: "Metropolis", sans-serif;
  background-blend-mode: screen;
  background: rgba(var(--white), 1);
}

h1 {
  font-weight: 700;
  font-size: calc(25px + 25 * (100vw - 320px) / 1600);
  line-height: 1;
  margin-bottom: 0;
}

h2 {
  font-weight: 600;
  font-size: calc(24px + 6 * (100vw - 320px) / 1600);
  line-height: calc(30px + 10 * (100vw - 320px) / 1600);
  margin-bottom: 0;
  display: inline-block;
}

h3 {
  font-weight: 500;
  font-size: calc(18px + 4 * (100vw - 320px) / 1600);
  line-height: 22px;
  margin-bottom: 0;
}

h4 {
  font-weight: 400;
  font-size: calc(14px + 6 * (100vw - 320px) / 1600);
  margin-bottom: 0;
}

h5 {
  font-weight: 400;
  font-size: calc(14px + 4 * (100vw - 320px) / 1600);
  margin-bottom: 0;
}

p {
  font-size: calc(12px + 6 * (100vw - 320px) / 1600);
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

li {
  list-style: none;
  display: inline-block;
  font-size: 14px;
}

/*********************
    Button css 
**********************/
.btn-solid {
  padding: calc(5px + 5 * (100vw - 320px) / 1600) calc(8px + 8 * (100vw - 320px) / 1600);
  display: flex;
  align-items: center;
  gap: 5px;
  border-radius: 6px;
  font-size: calc(12px + 3 * (100vw - 320px) / 1600);
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
  font-weight: 500;
  font-size: 16px;
  width: max-content;
}
.btn-solid:hover {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
}
.btn-solid:active {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
  border: none;
}
.btn-solid.btn-wide {
  padding: calc(5px + 5 * (100vw - 320px) / 1600) calc(20px + 20 * (100vw - 320px) / 1600);
}
.btn-solid .purchase-btn {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
}
.btn-solid .purchase-btn h4 {
  font-size: 16px;
}
.btn-solid .purchase-btn .cart {
  position: relative;
  width: 28px;
  height: 28px;
}
.btn-solid .purchase-btn .cart::after {
  content: "";
  position: absolute;
  top: 30px;
  right: -15px;
  border: 1px solid rgba(var(--content-color), 1);
  transform: rotate(90deg);
  width: 35%;
}

/***************************
    Title css 
****************************/
.title {
  margin-bottom: 15px;
}
.title h2 {
  margin-bottom: calc(10px + 5 * (100vw - 320px) / 1600);
  margin-top: calc(5px + 5 * (100vw - 320px) / 1600);
  font-size: calc(24px + 21 * (100vw - 320px) / 1600);
}
.title h3 {
  margin-bottom: calc(5px + 5 * (100vw - 320px) / 1600);
  margin-top: 4px;
  font-size: calc(22px + 6 * (100vw - 320px) / 1600);
  font-weight: 600;
  color: rgba(var(--secondary-color), 1);
}
.title p {
  width: 50%;
  color: rgb(82, 82, 108);
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
  opacity: 0.7;
  font-size: calc(14px + 4 * (100vw - 320px) / 1600);
  line-height: 1.5;
}
@media (max-width: 1199px) {
  .title p {
    width: 50%;
  }
}
@media (max-width: 991px) {
  .title p {
    width: 100%;
  }
}
.title .small-title {
  font-family: "caveat", sans-serif;
  color: #ff8d2f;
}

/************************
    modal css 
 ************************/
.app-btn-modal .modal-content {
  background-color: #fff;
}
.app-btn-modal .modal-content .modal-header {
  border-bottom: 1px solid rgb(237, 237, 237);
}
.app-btn-modal .modal-content .modal-header .btn-close {
  font-size: 14px;
}
.app-btn-modal .modal-content .modal-header .btn-close:focus {
  box-shadow: none;
}
.app-btn-modal .modal-content .modal-body {
  padding: 20px;
}
.app-btn-modal .modal-content .modal-body .app-buttons-list li {
  margin-bottom: 10px;
}
.app-btn-modal .modal-content .modal-body .app-buttons-list li:last-child {
  margin-bottom: 0;
}
.app-btn-modal .modal-content .modal-body .app-buttons-list li .app-buttons {
  padding: 10px;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  color: rgba(var(--black), 1);
  border-radius: 10px;
}
.app-btn-modal .modal-content .modal-body .app-buttons-list li .app-buttons .app-icon {
  width: 25px;
  height: 25px;
}

/************************
    header section 
 ************************/
header {
  position: absolute;
  width: 100%;
  padding-top: calc(10px + 20 * (100vw - 320px) / 1600);
  z-index: 2;
}

.navbar {
  justify-content: unset;
  align-items: center;
}
.navbar .btn {
  margin-left: auto;
}
.navbar .logo {
  height: calc(35px + 15 * (100vw - 320px) / 1600);
}
.navbar .navbar-toggler {
  padding: 0;
  border: 0;
}
.navbar .navbar-toggler .navbar-toggler-icon {
  width: auto;
  height: auto;
  color: rgba(var(--white), 0.3);
  border: 1px solid rgba(var(--white), 0.3);
  border-radius: 6px;
  margin-right: calc(10px + 10 * (100vw - 320px) / 1600);
  background-image: none;
}
.navbar .navbar-toggler .navbar-toggler-icon i {
  padding: 3px;
}
.navbar .navbar-toggler:focus {
  box-shadow: none;
}
@media (max-width: 991px) {
  .navbar .navbar-collapse {
    background-color: rgba(var(--black), 1);
    color: rgba(var(--white), 1);
    padding: 13px 17px;
    margin-top: 8px;
    border-radius: 8px;
  }
}
.navbar .navbar-nav {
  display: flex;
  gap: calc(20px + 20 * (100vw - 991px) / 929);
}
.navbar .navbar-nav .nav-item .nav-link {
  text-transform: capitalize;
  color: rgba(var(--content-color), 1);
  font-weight: 500;
  font-size: 18px;
  padding: 0;
}
.navbar .navbar-nav .nav-item .nav-link.active {
  color: rgba(var(--theme-color), 1);
}

/***********************
    animation section 
************************/
@keyframes bike {
  from {
    margin-top: 5%;
  }
  to {
    margin-top: 4%;
  }
}
@keyframes popcorn {
  from {
    margin-top: 5%;
  }
  to {
    margin-top: 4%;
  }
}
